'use strict';

/**
* Compute the hypotenuse.
*
* @module @stdlib/math-base-special-fast-alpha-max-plus-beta-min
*
* @example
* import hypot from '@stdlib/math-base-special-fast-alpha-max-plus-beta-min' ;
*
* var h = hypot( 5.0, 12.0 );
* // returns ~13.5
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import factory from './factory.js' ;


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
